@import './home.scss';
.add-item-container{
    display: flex;
    flex-wrap: wrap;
    text-align: left;

    .number-container{
        width:40%;
        display:block;
        background:#f5f5f5;
        border-right:10px solid #fff;
        border-bottom:10px solid #fff;
        box-sizing:border-box;
        position: relative;
        span{
            font-size:3.1em;
            position: absolute;
            top:50%;
            left:50%;
            transform: translate(-50%, -50%);
        }
    }
    .info-container{
        width:60%;
        box-sizing:border-box;
    }
    .button-container{
        width:100%;
        text-align:center;
    }
    
    input{
        padding: 8px 15px;
        margin-bottom: 10px;
        border-radius: 0px;
        &[type="text"]{
            border: 1px solid #eee;
            box-sizing: border-box;
            width: 100%;
            color:#666;
        }
        &[type="button"]{
            background-color: $highlight;
            border:none;
            color:#fff;
            margin-top:10px;
            width: 100%;
            cursor: pointer;
            padding-top: 10px;
            padding-bottom: 10px;
            font-weight: bold;
            &:hover{
                background-color: $highlight;
                opacity: 0.9;
            }
            &:active{
                opacity: 1.0;
            }
        }
        &:focus {
            outline-style:none;
            box-shadow:none;
        }

    }
}