.board-container {
  padding: 30px;
  padding-top: 0px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  position: relative; }
  .board-container .board-item {
    padding: 30px 30px 15px;
    border: 1px solid #eaeaea;
    width: calc(25% - 62px);
    position: relative;
    text-align: center; }
    .board-container .board-item.serving {
      background-color: #47ab43;
      border-color: #47ab43; }
      .board-container .board-item.serving .item-number {
        color: #FFFFFF; }
      .board-container .board-item.serving .purpose,
      .board-container .board-item.serving .item-status {
        color: #FFFFFF !important; }
    .board-container .board-item.reserved .item-number {
      color: #484848;
      opacity: 0.4; }
    .board-container .board-item .item-number {
      font-size: 3.5em;
      display: block; }
    .board-container .board-item .item-status {
      padding: 30px;
      text-align: center; }
      .board-container .board-item .item-status.reserved {
        color: #ccc;
        font-weight: bold; }
      .board-container .board-item .item-status .purpose {
        color: #484848;
        opacity: 0.4;
        font-weight: normal; }
    .board-container .board-item:not(.serving):not(.reserved):hover {
      box-shadow: 0 0 18px rgba(6, 6, 6, 0.2);
      cursor: pointer; }
    .board-container .board-item .time-remaining {
      width: 100%;
      text-align: center;
      margin-top: 15px; }
      .board-container .board-item .time-remaining span {
        background: #359d31;
        padding: 5px 10px; }
    .board-container .board-item .percent-remaining {
      height: 10px;
      background-color: #42d23d;
      position: absolute;
      bottom: 0px;
      left: 0px;
      text-align: center; }

@media (max-width: 991px) {
  .board-container .board-item {
    width: calc(50% - 62px); } }

@media (max-width: 450px) {
  .board-container .board-item {
    width: calc(100% - 62px); } }

