/* Color Theme Swatches in Hex */
$white-space: #FFFFFF;
$highlight: #47ab43;
$dark-background: #eaeaea;

$light-foreground: #FFFFFF;
$color-foreground: #F2F2F5;
$neutral-foreground: #484848;
$disabled-foreground: #ccc;

$mobile: 991px;
$mobile-xs: 450px;

%transition{
    -webkit-transition: all 0.3s !important; /* Safari */
    transition: all 0.3s !important;
}
 
$global-pad: 30px;

h1{
    margin:0px;
    font-size: 3.0em;
}
h2{
    margin:0px;
    font-size: 2.5em;
}
h3{
    margin:0px;
    font-size: 2.0em;
}
h4{
    margin:0px;
    font-size: 1.5em;
}
h5{
    margin:0px;
    font-size: 1.2em;
}

html{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 14px;
    body{
        background: $dark-background;
        .container{
            width: 80%;
            margin:auto;
            background-color: $white-space;
        }
    }
}

@media(max-width:$mobile){
    
html{
    body{
        .container{
            width: 95%;
        }
    }
}
}