@import './home.scss';
 
.board-container{
    padding: $global-pad;
    padding-top: 0px;
    display:flex;
    flex-wrap: wrap;
    position: relative;
    @extend %transition;
    .board-item{
        padding: $global-pad $global-pad 15px;
        border: 1px solid $dark-background;
        width: calc(25% - 62px);
        position: relative;
        text-align: center;
        @extend %transition;
        &.serving{
            background-color:$highlight;
            border-color: #47ab43;
            .item-number{
                color:$light-foreground;
            }
            .purpose,
            .item-status{
                color: $light-foreground !important;
            }
        }

        &.reserved{
            .item-number{
                color:$neutral-foreground;
                opacity:0.4;
            }
        }

        .item-number{
            font-size: 3.5em;
            display: block;
        }
        .item-status{
            padding: $global-pad;
            text-align: center;
            &.reserved{
                color: $disabled-foreground;
                font-weight: bold;
            }
            .purpose{
                color:$neutral-foreground;
                opacity:0.4;
                font-weight: normal;
            }
        }
        &:not(.serving):not(.reserved):hover{
            // background-color: $dark-background;
            box-shadow: 0 0 18px rgba(6, 6, 6, 0.2);
            cursor: pointer;
        }
        .time-remaining{
            width:100%;
            text-align: center;
            margin-top:15px;
            span{
                background: #359d31;
                padding:5px 10px;
            }
        }
        .percent-remaining{
            @extend %transition;
            height: 10px;
            background-color: #42d23d;
            position: absolute;
            bottom: 0px;
            left:0px;
            text-align: center;
        }
    }
}

@media(max-width:$mobile){
    .board-container{
        .board-item{
            width: calc(50% - 62px);
        }
    }
}

@media(max-width:$mobile-xs){
    .board-container{
        .board-item{
            width: calc(100% - 62px);
        }
    }
}